import React from 'react';
import PropTypes from 'prop-types';
import ActivityReportingContainer from '../../ActivityReportingComponent/ActivityReportingContainer';
import DownloadReportContainer from '../../ReportSidePaneComponents/DownloadReportForm/DownloadReportContainer';
import ScheduledReportsTableContainer from '../../ReportSidePaneComponents/ScheduledReportsTable/ScheduledReportsTableContainer';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RetailTradeReportComponent.css';

const retailTradeReportClasses = getBEMClasses(['retail-trade-report']);

const RetailTradeReportComponent = () => {
  return (
    <div className={retailTradeReportClasses()}>
      <div className={retailTradeReportClasses('content')}>
        <ActivityReportingContainer />
        <ScheduledReportsTableContainer />
        <DownloadReportContainer />
      </div>
    </div>
  );
};

RetailTradeReportComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailTradeReportComponent;



